import React, { Component } from 'react';
import $ from 'jquery'
var iso;
var works = [
  {
    'title':'TIC_TAC_TOE',
    'description':`A tic tac toe game using the minmax algorith\n
    and illustrated using aseprite`,
    'tags':["design","programming"],
    'imageref':"tictactoe",
    'overlay':'chartreuse',
    'link':'tictactoe/tictactoe.html',
    'color':'black'
  },
  {
    'title':'drum_machine',
    'description':`A drumbox built with react.`,
    'tags':["programming"],
    'imageref':"drum_machine",
    'overlay':'dodgerblue',
    'link':'drum_machine/index.html',
    'color':'white'
  },
  {
    'title':'Markdown Previewer',
    'description':`A markdown previewer using react and marked.js`,
    'tags':["programming"],
    'imageref':"markdownpreviewer",
    'overlay':'skyblue',
    'link':'markdown_previewer/index.html',
    'color':'black'
  },
  {
    'title':'Ravage issue 15',
    'description':`I designed the 15th issue of the magazine 'Ravage'.`,
    'tags':["design"],
    'imageref':"couvRavage2",
    'overlay':'cornsilk',
    'link':'https://docs.wixstatic.com/ugd/7b6eb2_1e6bfb8a90d04e2483b1b2be233d3f65.pdf',
    'color':'black'
  },
  {
    'title':'Ravage issue 14',
    'description':`I designed the front page of the 14th issue of the magazine 'Ravage' as well as the pages 5 to 19.`,
    'tags':["design"],
    'imageref':"couvRavage1",
    'overlay':'brown',
    'link':'https://docs.wixstatic.com/ugd/7b6eb2_2c140c1f199b4ac98ce1d9fd63e1058b.pdf',
    'color':'white'
  },
  {
    'title':'Pomodoro Clock',
    'description':`A pomodoro clock built with javascript.`,
    'tags':["programming"],
    'imageref':"pomodoroclock",
    'overlay':'tomato',
    'link':'pomodoro clock/index.html',
    'color':'white'
  },
  {
    'title':'Simon',
    'description':`A simple simon game`,
    'tags':["programming"],
    'imageref':"simon",
    'overlay':'darkorange',
    'link':'simon/index.html',
    'color':'white'
  },
  {
    'title':'BlackJack.py',
    'description':`A blackjack game created with python 3.`,
    'tags':["programming"],
    'imageref':"blackjack",
    'overlay':'darkolivegreen',
    'link':'simon/index.html',
    'color':'white'
  }
]
class WorkWrapper extends Component{
  handleButtonClick(e){
    $('.workButton').removeClass('active');
    $(e.target).addClass('active');
    let tag= $(e.target).attr('tag');
    if (tag==='all'){
      iso.arrange({
        filter:"*"
      });
      return
    }
    let classfilter = '.'+tag
    iso.arrange({
      filter:classfilter,
    })
  }

  componentDidMount(){
    var resizeTimer;

    $(window).on('resize', function(e) {

      clearTimeout(resizeTimer);
      resizeTimer = setTimeout(function() {

        $('.workButto,[tag="all"]').click()

      }, 250);

    });
    var elem = document.querySelector('.pageWrapper');
    iso = new window.Isotope( elem, {
      // options
      itemSelector: '.workPage',
      //layoutMode: 'fitRows',
      masonry: {
        gutter: 10
      }
    });
  }

  render(){

    let workpages = works.map(obj=>{
      return (
        <Workpage
          key={obj.title}
          id={obj}
          title={obj['title']}
          imageref={obj['imageref']}
          description={obj['description']}
          tags={obj['tags']}
          overlay={obj['overlay']}
          link={obj['link']}
          color={obj['color']}
        />
      )
    })



    return(
      <div className='workwrapper' id='workwrapper'>
        <div className='bar'>
          <h2> Work </h2>
        </div>
        <hr/>
        <div className = 'worknav'>
          <ul>
            <li><button className='workButton active' tag='all' onClick={this.handleButtonClick}>All</button><hr/></li>
            <li><button className='workButton' tag='design' onClick={this.handleButtonClick}>Design</button><hr/></li>
            <li><button className='workButton' tag='programming' onClick={this.handleButtonClick}>Programming</button><hr/></li>
          </ul>
        </div>
        <div className='pageWrapper' ref={dom=>this.pageWrapper = dom}>
          {workpages}
        </div>
      </div>
    )
  }
}

class Workpage extends Component{
  constructor(props){
    super(props)
    let img = require("../img/"+this.props.imageref+".png");
    this.state={
      img:img,
      background:`url('${img}')`,
    }
    this.handleHover=this.handleHover.bind(this)
    this.handleMouseLeave=this.handleMouseLeave.bind(this);
    this.handleClick=this.handleClick.bind(this);
  }
  componentDidMount(){
    this.props.tags.forEach((tag)=>{
      this.dom.classList.add(tag);
    })
  }

  handleHover(e){
    this.setState({
      background:'#55ad9b'
    })
  }

  handleClick(e){
    this.setState({
      background:'red',
    })
  }

  handleMouseLeave(e){
    this.setState({
      background:`url('${this.state.img}')`
    })
  }
  render(){
    let cssobj = {
      background:this.state.background
    }
    return (
      <div className='workPage' tags={this.props.tags} ref={(dom)=>this.dom=dom}>
        <div className='overlay' style={{backgroundColor:this.props.overlay,color:this.props.color}}>
          <div className='descriptionWrapper'>
            <p className='title' >{this.props.title}</p>
            <p className='description'>{this.props.description}</p>
          </div>
          <a href={this.props.link}><button>Learn more</button></a>
        </div>
        <div className='workContent' style={cssobj}>

        </div>
      </div>
    )
  }
}

export default WorkWrapper



